"use client";

import { useParams } from "next/navigation";
import { motion } from "framer-motion";
import { blogPosts } from "@/data/blogPosts";
import Navbar from "@/components/layout/Navbar";
import Footer from "@/components/layout/Footer";
import Image from "next/image";
import { Calendar, User, ArrowLeft, Share2 } from "lucide-react";
import Link from "next/link";
import ReactMarkdown from "react-markdown";

export default function BlogPostDetail() {
    const params = useParams();
    const slug = params.slug as string;
    const post = blogPosts.find((p) => p.slug === slug);

    if (!post) {
        return (
            <main className="relative min-h-screen bg-background flex flex-col items-center justify-center p-4">
                <h1 className="text-4xl font-bold mb-4">Post Not Found</h1>
                <Link href="/blog" className="text-accent flex items-center gap-2">
                    <ArrowLeft className="w-4 h-4" /> Back to Blog
                </Link>
            </main>
        );
    }

    return (
        <main className="relative min-h-screen bg-background text-foreground">
            <Navbar />

            <article className="pt-32 pb-24 overflow-hidden">
                {/* Post Header */}
                <section className="container px-4 mx-auto mb-16">
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        className="max-w-4xl mx-auto"
                    >
                        <Link
                            href="/blog"
                            className="inline-flex items-center gap-2 text-sm font-bold text-accent uppercase tracking-widest mb-8 hover:translate-x-[-4px] transition-transform"
                        >
                            <ArrowLeft className="w-4 h-4" /> Back to Insights
                        </Link>

                        <div className="flex items-center gap-4 mb-6">
                            <span className="px-4 py-1.5 rounded-full bg-accent/10 text-xs font-bold text-accent border border-accent/20">
                                {post.category}
                            </span>
                            <div className="flex items-center gap-2 text-xs text-muted-foreground">
                                <Calendar className="w-3.5 h-3.5" /> {post.date}
                            </div>
                        </div>

                        <h1 className="text-4xl font-bold tracking-tight md:text-6xl mb-8 leading-[1.1]">
                            {post.title}
                        </h1>

                        <div className="flex items-center justify-between border-y border-white/5 py-8">
                            <div className="flex items-center gap-4">
                                <div className="w-12 h-12 rounded-full bg-accent/20 border border-accent/20 flex items-center justify-center text-accent">
                                    <User className="w-6 h-6" />
                                </div>
                                <div>
                                    <p className="font-bold">{post.author.name}</p>
                                    <p className="text-xs text-muted-foreground">{post.author.role}</p>
                                </div>
                            </div>
                            <button className="p-3 rounded-full glass-card border-white/5 hover:border-accent/40 text-muted-foreground hover:text-accent transition-all">
                                <Share2 className="w-5 h-5" />
                            </button>
                        </div>
                    </motion.div>
                </section>

                {/* Hero Image */}
                <section className="container px-4 mx-auto mb-20">
                    <motion.div
                        initial={{ opacity: 0, scale: 0.95 }}
                        animate={{ opacity: 1, scale: 1 }}
                        transition={{ duration: 0.8 }}
                        className="relative aspect-[21/9] rounded-[3rem] overflow-hidden glass-card border-white/5"
                    >
                        <Image
                            src={post.image}
                            alt={post.title}
                            fill
                            className="object-cover opacity-80"
                        />
                        <div className="absolute inset-0 bg-gradient-to-t from-background via-transparent to-transparent" />
                    </motion.div>
                </section>

                {/* Content Body */}
                <section className="container px-4 mx-auto">
                    <div className="max-w-3xl mx-auto">
                        <div className="prose prose-invert prose-blue max-w-none">
                            <ReactMarkdown
                                components={{
                                    h2: ({ ...props }) => <h2 className="text-3xl font-bold mt-12 mb-6" {...props} />,
                                    h3: ({ ...props }) => <h3 className="text-2xl font-bold mt-8 mb-4" {...props} />,
                                    p: ({ ...props }) => <p className="text-lg text-muted-foreground leading-relaxed mb-6" {...props} />,
                                    ul: ({ ...props }) => <ul className="space-y-4 mb-8 list-none !pl-0" {...props} />,
                                    li: ({ ...props }) => (
                                        <li className="flex items-start gap-3 text-lg text-muted-foreground" {...props}>
                                            <div className="w-1.5 h-1.5 rounded-full bg-accent mt-3 shrink-0" />
                                            <span>{props.children}</span>
                                        </li>
                                    ),
                                    em: ({ ...props }) => <em className="text-accent italic" {...props} />,
                                }}
                            >
                                {post.content}
                            </ReactMarkdown>
                        </div>

                        <div className="mt-20 p-12 rounded-[2.5rem] glass-card border-accent/20 bg-accent/5 relative overflow-hidden">
                            <div className="absolute top-0 right-0 w-64 h-64 bg-accent/5 blur-[80px] -z-10" />
                            <h4 className="text-2xl font-bold mb-4">Ready to elevate your digital presence?</h4>
                            <p className="text-muted-foreground mb-8">Our team is ready to help you implement these strategies and build a brand that lasts.</p>
                            <Link
                                href="/contact"
                                className="px-8 py-4 bg-accent text-white font-bold rounded-2xl border-glow flex items-center justify-center gap-2 hover:bg-accent/90 transition-all w-fit"
                            >
                                Schedule A Consultation
                            </Link>
                        </div>
                    </div>
                </section>
            </article>

            <Footer />
        </main>
    );
}
